
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import api from '../axios'

export default {
directives: {
TransferDom
},
components: {
XInput,
XButton,
Group,
Confirm
},
mounted () {
this.phoneValid = this.$refs.phoneValid.valid
this.passwordValid = this.$refs.passwordValid.valid
},
data () {
return {
phone: '13877907810',
password: '123456',
min: 6,
max: 26,
phoneValid: true,
passwordValid: true,
checkPasswordValid: function (value) {
return {
valid: value.length >= this.min
}
}
}
},
methods: {
checkValid () {
this.phoneValid = !this.$refs.phoneValid.valid
this.passwordValid = !this.$refs.passwordValid.valid
},
showAlert () {
const _this = this
this.$vux.alert.show({
title: '注册成功！',
content: '将在1秒后跳转至登录页面...',
buttonText: '立即前往',
onHide () {
_this.$router.push('/login')
}
})
},
showAlertAuto () {
const _this = this
this.showAlert()
setTimeout(() => {
_this.$vux.alert.hide()
_this.$router.push('/login')
}, 1000)
},
submit () {
if (!this.phoneValid && !this.passwordValid) {
let opt = {
phone: this.phone,
password: this.password
}
api.userRegister(opt).then(({data}) => {
if (data.success) {
this.showAlertAuto()
} else {
this.$vux.confirm.show({
title: '错误警告',
content: '此账户已存在'
})
}
}).catch((err) => {
console.log(err)
})
} else {
console.log('Error Submit')
return false
}
}
}
}



